<template>
  <div class="course-live-lesson">
    <head-top title="课堂实况">
    </head-top>

    <!--    <van-search v-model="keyWord" placeholder="请输入标签关键词" @search="onSearch"/>-->

    <div style="position: relative;">
      <van-tabs v-model="typeSelectedIndex" sticky color="rgba(0, 203, 171, 1)" :ellipsis="false"
                title-inactive-color="#aaa"
                :line-height="2" @change="handle">
        <van-tab v-for="(item, index) in typeOptions" :key="index" :name="item.index" :title="item.title"
                 style="flex-basis: 22%;">
          <!--          {{item.title}}-->
        </van-tab>
      </van-tabs>

      <!--  快捷菜单栏-->
      <div class="tab_right" @click="typeOptionsShow = !typeOptionsShow">
        <van-icon :name="typeOptionsShow ? 'arrow-up' : 'arrow-down'" size="20">
        </van-icon>
      </div>
      <div v-show="typeOptionsShow" class="tab_overlay" @click="typeOptionsShow = false"></div>
      <div v-show="typeOptionsShow" class="tab_options">
        <van-tag plain round class="options_item" v-for="(item, index) in typeOptions" :key="index"
                 :type="typeSelectedIndex === item.index ? 'success' : 'default'" @click="tabChange(item)">
          {{item.title}}
        </van-tag>
      </div>
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <van-cell-group v-if="playList && playList.length>0">
        <van-cell class="cell_main" v-for="(sub, k) in playList" :key="k" is-link @click="select(sub)">
          <template #icon>
            <van-icon class="cell_icon" class-prefix="iconfont icon-weike1"></van-icon>
          </template>
          <template #title>
            <div class="line_title">{{sub.title}}</div>
          </template>
          <template #label>
            <div class="line_title flex-between">
              <span>{{ sub.start | formatDate(sub.start)}} </span>
              <span class="line_mark">{{sub.remark}}</span>
            </div>
          </template>
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>

  </div>
</template>

<script>
import { recordLessonType, recordLessonTypeName } from "@/utils/enum";

export default {
  name: "index",
  components: {},
  data () {
    return {
      typeOptions: [],
      typeSelectedIndex: 0,
      typeOptionsShow: false,
      typeValue: "",

      isLoading: false,
      keyWord: "",
      liveList: [],
      // 是否处于课中
      isInCourse: this.$route.path.indexOf("history") === -1
    };
  },
  computed: {
    playList () {
      return this.liveList.filter(r => !this.typeValue ? r : r.typeValue === this.typeValue);
    }
  },
  mounted () {
    this.typeOptions = [];
    this.typeOptions.push({ title: "全部", value: "", index: 0 });
    let index = 0;
    for (const item in recordLessonType) {
      index++;
      this.typeOptions.push({
        title: recordLessonTypeName(recordLessonType[item]),
        value: recordLessonType[item],
        index
      });
    }
    this.typeSelectedIndex = 0;
    this.onSearch();
  },
  methods: {
    onSearch () {
      this.dataList = [];
      this.liveList = [];
      this.isLoading = true;
      this.$mainHttp.recordLesson.dataList({
        key: this.keyWord,
        ciID: this.$store.getters.courseID
      }).then(res => {
        if (res.Result) {
          if (this.isInCourse) {
            res.Datas.sort((a, b) => {
              return a["RLStart"] > b["RLStart"] ? 1 : -1;
            });
          }

          res.Datas.forEach((item, index) => {
            let sub = {
              id: item["RLID"],
              deviceID: item["DeviceID"],
              title: item["RLName"],
              start: item["RLStart"],
              end: item["RLEnd"],
              typeValue: item["RLType"] + "",
              remark: item["RLLabel"],
              state: item["RLEstate"],
              allow: item["AllowSee"]
              // src: this.$mainHttp.common.video(item["RLID"], ".mp4"),
              // poster: defaultPost
            };
            this.liveList.push(sub);
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    select (item) {
      this.$router.push({
        path: this.$route.path.indexOf("/history") > -1 ? "/history/livePlayback" : "/livePlayback",
        query: item
      });
    },
    tabChange (item) {
      this.typeSelectedIndex = item.index;
    },
    handle (val) {
      this.typeValue = this.typeOptions[val].value;
      setTimeout(() => {
        this.typeOptionsShow = false;
      }, 200);
    }
  },
  watch: {
    typeOptionsShow: {
      immediate: true,
      handler: (n) => {
        document.body.style.overflowY = n ? "hidden" : "auto";
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import url("./../../styles/common/base.less");

.course-live-lesson {
  background-color: #fff;

  /deep/ .van-tabs--line .van-tabs__wrap {
    width: calc(100vw - 40px);

    .van-tab {
      padding: 0 15px;
    }
  }

  .tab_right {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 9;
    background: #fff;
    width: 40px;
    height: 44px;
    margin: 0 auto;

    .van-icon {
      line-height: 44px;
    }
  }

  .tab_overlay {
    position: absolute;
    top: 44px;
    left: 0;
    z-index: 6;
    width: 100%;
    height: calc(100vh);
    background-color: rgba(0, 0, 0, 0.5);
  }

  .tab_options {
    position: absolute;
    z-index: 99;
    background-color: #fcfcfc;
    top: 44px;
    left: 0;
    width: calc(100% - 10px);
    padding: 10px 5px;
    text-align: left;
    border-radius: 0 0 15px 15px;

    .options_item {
      margin: 5px 0 5px 10px;
      padding: 5px 9px;
    }
  }

  .line_mark {
    text-align: right;
    width: calc(100% - 30px - 70px - 30px);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>



